<template>
  <div class="help">
    <app-table
      title="Suporte"
      :currentPage="currentPage"
      :headers="headers"
      :items="itemsModel"
      :loading="loading"
      :qtdPages="qtdPages"
      @onPageChanged="changePage"
      @onActionClicked="callAction"
    />
  </div>
</template>

<script>
import AppTable from '@/components/moderators/Table';

import DateMixin from '@/mixins/DateMixin';
import SnackbarMixin from '@/mixins/SnackbarMixin';

export default {
  components: { AppTable },
  mixins: [ DateMixin, SnackbarMixin ],
  data: () => ({
    currentPage: null,
    headers: [
      { text: '#', value: 'id', align: 'center', width: '60' },
      { text: 'Resolvido', value: 'actions' },
      { text: 'Data', value: 'created_at' },
      { text: 'Nome', value: 'name' },
      { text: 'E-mail', value: 'email' },
      { text: 'Mensagem', value: 'message' },
      
    ],
    items: [],
    loading: true,
    qtdPages: 1,
  }),
  computed: {
    itemsModel () {
      return this.items.map((item) => {
        const date = new Date(item.created_at);
        const actions = [
          {
            class:  item.resolved ? 'success' : 'gray1',
            action:  item.resolved ? '' : 'resolve',
            text: 'Contactado'
          }
        ];

        return {
          id: item.id,
          actions: actions,
          name: item.name,
          responsable: item.responsable,
          email: item.email,
          message: item.message,
          created_at: this.formatDateTimeToStringPtBr(date)
        };
      });
    }
  },
  mounted () {
    this.getHelpRequests()
  },
  methods: {
    getHelpRequests () {  
      let url = `${this.$url_api}/help?paginate=true&orderBy=created_at&order=desc&`
      
      if (this.currentPage) url += `page=${this.currentPage}&`;
      
      this.$axios(url)
      .then(response => {
        if (response.status === 200) {
          if (response.data.codigo === 200) {
            const responseData = response.data.corpo;
            this.items = responseData.data;
            this.qtdPages = responseData.last_page;
            this.currentPage = responseData.current_page
          }
        }
      }).finally(() => this.loading = false);
    },
    changePage(page) {
      this.currentPage = page;
      this.getHelpRequests();
    },
    callAction(action, item) {
      if (action == 'resolve') {
        this.resolve(item.id)
      }
    },
    resolve (id) {
      this.$axios.patch(`${this.$url_api}/help/${id}/resolve`)
      .then(response => {
        if (response.status === 200) {
          if (response.data.codigo === 200) {
            if (response.data.corpo) {
              const index = this.itemsModel.findIndex((item) => item.id == id);
              if (index > -1) {
                this.items[index].resolved = true;
              }
              this.showSnackbar('Ticket atualizado!')
            } else this.setError()
          } else this.setError()
        } else this.setError()
      }).catch(() => this.setError());
    },
    setError() {
      this.showSnackbarErro('No foi possvel atualizar a ticket!');
    },
  }
}
</script>